'use client'

import { useEffect, useState } from 'react'
import { Bell, User, Calendar, Edit } from 'lucide-react'
import { Button } from '@/components/ui/button'
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import Image from 'next/image'

type SeasonData = {
  cereals: string[];
  grams?: string[];
  vegetables: string[];
  fruits: string[];
};


export function DashboardHeader() {
  const [openSeasonModal, setOpenSeasonModal] = useState(false)
    const [openUserModal, setOpenUserModal] = useState(false)

  const [user, setUser] = useState<any>(null)

  const currentDate = new Date()

  // --------------------------
  // Load USER DATA from localStorage
  // --------------------------
  useEffect(() => {
    const data = localStorage.getItem('userData')
    if (data) {
      setUser(JSON.parse(data))
    }
  }, [])

  // --------------------------
  // Season Calculation Logic
  // --------------------------
function getSeasonInfo() {
  const month = currentDate.getMonth() + 1
  const day = currentDate.getDate()

  let season = ''
  let image = ''
  let data: SeasonData = {
    cereals: [],
    vegetables: [],
    fruits: []
  }

  const isRabi =
    (month === 10 && day >= 15) ||
    month === 11 ||
    month === 12 ||
    month === 1 ||
    month === 2 ||
    month === 3 ||
    (month === 4 && day <= 10)

  if (isRabi) {
    season = 'Rabi Season'
    image = '/seasons/rabi.jpg'
    data = {
      cereals: ['Wheat', 'Barley', 'Oats','Raya'],
      grams: ['Chickpeas','Lentil(Masoor)'],
      vegetables: ['Spinach', 'Carrot', 'Cauliflower'],
      fruits: ['Orange', 'Pomegranate', 'Guava']
    }
  } else {
    season = 'Kharif Season'
    image = '/seasons/kharif.jpg'
    data = {
      cereals: ['Cotton','Rice', 'Maize', 'Sorghum'],
      vegetables: ['Okra', 'Cucumber', 'Bitter Gourd'],
      grams: ['Mash', 'Moong'],

      fruits: ['Mango', 'Banana', 'Papaya']
    }
  }

  return { season, data, image }
}


  const { season, data, image } = getSeasonInfo()

  return (
    <>
      <header className="border-b border-border bg-card">
        <div className="flex items-center justify-between px-6 md:px-8 py-4">
          <div>
            <h2 className="text-2xl font-bold text-foreground">
              Farm Management Dashboard
            </h2>

            <p className="text-sm text-muted-foreground mt-1">
              {currentDate.toLocaleDateString('en-US', {
                year: 'numeric',
                month: 'long',
                day: 'numeric',
              })}
            </p>
          </div>

          <div className="flex items-center gap-4">
            {/* Current Season Button */}
            <button
              onClick={() => setOpenSeasonModal(true)}
              className="flex items-center gap-2 px-3 py-2 bg-muted rounded-lg hover:bg-muted/70 transition"
            >
              <Calendar size={18} className="text-muted-foreground" />
              <span className="text-sm font-medium">{season}</span>
            </button>

            <Button variant="ghost" size="icon" className="relative">
              <Bell size={20} />
              <span className="absolute top-1 right-1 w-2 h-2 bg-destructive rounded-full" />
            </Button>

               {/* USER PROFILE BUTTON */}
            <Button variant="ghost" size="icon" onClick={() => setOpenUserModal(true)}>
              <User size={20} />
            </Button>
          </div>
        </div>
      </header>

      {/* ---------------------- */}
      {/* Season Modal */}
      {/* ---------------------- */}
      <Dialog open={openSeasonModal} onOpenChange={setOpenSeasonModal}>
        <DialogContent className="p-0 overflow-hidden">
          
          {/* Season Image */}
          <div className="w-full h-40 relative">
            <Image
              src={image}
              alt={season}
              fill
              className="object-cover"
            />
          </div>

          <div className="p-6">
            <DialogHeader>
              <DialogTitle className="text-2xl font-bold">
                {season}
              </DialogTitle>
            </DialogHeader>

            <div className="space-y-4 mt-4">
              <div>
                <h4 className="font-semibold text-lg">Cereals</h4>
                <p className="text-muted-foreground">{data.cereals.join(', ')}</p>
              </div>

              {data.grams && (
                <div>
                  <h4 className="font-semibold text-lg">Grams / Pulses</h4>
                  <p className="text-muted-foreground">{data.grams.join(', ')}</p>
                </div>
              )}

              <div>
                <h4 className="font-semibold text-lg">Vegetables</h4>
                <p className="text-muted-foreground">{data.vegetables.join(', ')}</p>
              </div>

              <div>
                <h4 className="font-semibold text-lg">Fruits</h4>
                <p className="text-muted-foreground">{data.fruits.join(', ')}</p>
              </div>
            </div>
          </div>

        </DialogContent>
      </Dialog>

       {/* ---------------------- */}
      {/* USER DATA MODAL */}
      {/* ---------------------- */}
      {/* <Dialog open={openUserModal} onOpenChange={setOpenUserModal}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle className="text-xl font-bold">User Information</DialogTitle>
          </DialogHeader>

          {user ? (
            <div className="space-y-3 mt-3">
              <p><strong>Name:</strong> {user.firstName} {user.lastName}</p>
              <p><strong>Email:</strong> {user.email}</p>
              <p><strong>Phone:</strong> {user.phoneNumber}</p>
              <p><strong>Role:</strong> {user.roleDisplay || user.role}</p>
            </div>
          ) : (
            <p className="text-muted-foreground">No user data found</p>
          )}
        </DialogContent>
      </Dialog> */}


      <Dialog open={openUserModal} onOpenChange={setOpenUserModal}>
  <DialogContent className="sm:max-w-md">
    <DialogHeader className="space-y-2">
      <div className="flex items-center justify-between">
        <DialogTitle className="text-2xl font-semibold tracking-tight">
          User Profile
        </DialogTitle>
        <div className="flex h-10 w-10 items-center justify-center rounded-full bg-primary/10">
          <User className="h-5 w-5 text-primary" />
        </div>
      </div>
      <DialogDescription>
        Detailed information about the user account
      </DialogDescription>
    </DialogHeader>

    <div className="mt-6 space-y-5">
      {user ? (
        <>
          {/* User Info Section */}
          <div className="space-y-4">
            <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
              {/* Name Field */}
              <div className="space-y-2">
                <label className="text-sm font-medium text-muted-foreground">
                  Full Name
                </label>
                <div className="rounded-lg border bg-card px-4 py-3">
                  <p className="font-medium">
                    {user.firstName} {user.lastName}
                  </p>
                </div>
              </div>

              {/* Email Field */}
              <div className="space-y-2">
                <label className="text-sm font-medium text-muted-foreground">
                  Email Address
                </label>
                <div className="rounded-lg border bg-card px-4 py-3">
                  <p className="font-medium">{user.email}</p>
                </div>
              </div>

              {/* Phone Field */}
              <div className="space-y-2">
                <label className="text-sm font-medium text-muted-foreground">
                  Phone Number
                </label>
                <div className="rounded-lg border bg-card px-4 py-3">
                  <p className="font-medium">
                    {user.phoneNumber || <span className="text-muted-foreground italic">Not provided</span>}
                  </p>
                </div>
              </div>

              {/* Role Field */}
              <div className="space-y-2">
                <label className="text-sm font-medium text-muted-foreground">
                  User Role
                </label>
                <div className="rounded-lg border bg-card px-4 py-3">
                  <div className="flex items-center gap-2">
                    <div className="h-2 w-2 rounded-full bg-primary" />
                    <p className="font-medium">{user.roleDisplay || user.role}</p>
                  </div>
                </div>
              </div>
            </div>

            {/* Additional Details Section (Optional) */}
            <div className="mt-6 pt-6 border-t">
              <h4 className="mb-3 text-sm font-semibold text-muted-foreground uppercase tracking-wide">
                Account Information
              </h4>
              <div className="grid grid-cols-1 gap-3 text-sm">
                <div className="flex items-center justify-between py-2">
                  <span className="text-muted-foreground">Account Status</span>
                  <span className="inline-flex items-center rounded-full bg-green-50 px-2.5 py-0.5 text-xs font-medium text-green-800">
                    <div className="mr-1 h-1.5 w-1.5 rounded-full bg-green-600" />
                    Active
                  </span>
                </div>
                <div className="flex items-center justify-between py-2 border-b">
                  <span className="text-muted-foreground">Last Active</span>
                  <span className="font-medium">2 hours ago</span>
                </div>
              </div>
            </div>
          </div>
        </>
      ) : (
        <div className="flex flex-col items-center justify-center py-10 text-center">
          <div className="mb-4 flex h-16 w-16 items-center justify-center rounded-full bg-muted">
            <User className="h-8 w-8 text-muted-foreground" />
          </div>
          <h3 className="mb-2 text-lg font-semibold">No User Data</h3>
          <p className="text-muted-foreground max-w-sm">
            Unable to retrieve user information. The user may not exist or you may not have permission to view this data.
          </p>
        </div>
      )}
    </div>

    <DialogFooter className="mt-8">
      <Button variant="outline" onClick={() => setOpenUserModal(false)}>
        Close
      </Button>
    </DialogFooter>
  </DialogContent>
</Dialog>
    </>
  )
}
